body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.p-staff-blog__header {
  text-align: center;
}

.p-staff-blog-block {
  background-color: rgba(175, 158, 151, 0.6);
  width: 100vw;
  margin-left: calc(50% - 50vw);
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}

.p-staff-blog__textblock {
  padding-top: 0.625rem;
  margin-top: 1.875rem;
}
@media screen and (min-width: 768px) {
  .p-staff-blog__textblock {
    padding-top: initial;
    margin-top: 2rem;
  }
}

.p-staff-blog__text {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .p-staff-blog__text-sp {
    display: none;
  }
}

.p-staff-blog__card {
  margin: 1.25rem auto 0 auto;
}
@media screen and (min-width: 768px) {
  .p-staff-blog__card {
    max-width: 62.5rem;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 2rem auto 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .p-staff-blog-card__block:nth-child(n+2) {
    margin-left: 3.5rem;
  }
}

@media screen and (min-width: 768px) {
  .p-staff-blog-card__block:nth-child(4) {
    margin-left: initial;
  }
}

@media screen and (min-width: 768px) {
  .p-staff-blog-card__block:nth-child(n+4) {
    margin-top: 2.5rem;
  }
}

@media screen and (min-width: 768px) {
  .p-staff-blog-card__block {
    width: 18.5rem;
  }
}

.p-staff-blog__button-block {
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  text-align: center;
  text-transform: uppercase;
}

.p-staff-blog__button {
  background-color: #F7F7F7;
  color: #4D4D4D;
  border: 2px solid #4D4D4D;
}